import Utils from '../utils';

const ajax = {
  get: (url, callback) => {
    // ajax get
    if (typeof url !== 'string') throw new TypeError('');
    if (!(callback instanceof Function)) throw new TypeError('');
    const xmlhttp = (window.XMLHttpRequest) ? new XMLHttpRequest() : new ActiveXObject('Microsoft.XMLHTTP');
    xmlhttp.open('GET', url, true);
    xmlhttp.send();
    xmlhttp.onreadystatechange = (res) => {
      if (xmlhttp.readyState !== 4 || xmlhttp.status !== 200) return false;
      return callback(res);
    };
  },
};

const pjax = {
  get: (url) => {
    // promise ajax get
    if (!Utils.isString(url)) { throw new TypeError('url '); }
    const promise = new Promise((resolve) => {
      ajax.get(url, (result) => {
        resolve(result.target.response);
      });
    });
    return promise;
  },
};

class Component {
  // scriptjs  templatestyle html
  constructor(param) {
    // param.querydocument.querySelector
    // param.templateHtmlElement
    // param.style<style>
    if (typeof param !== 'object') { throw new TypeError('paramobject'); }
    if (!Utils.isString(param.query)) { throw new TypeError('param.queryquerySelector'); }
    if (!Utils.isElement(param.template)) { throw new TypeError('param.templateHtmlElement'); }
    const result = Object.assign(this, param);
    this.selector = document.querySelector(this.query) || document.querySelector(`*[query=${this.query}]`);
    if (!Utils.isElement(this.selector)) { throw new TypeError(` ${param.query} `); }
    try {
      // 
      if (Utils.isFunction(result.created)) {
        result.created();
      }
      result.implant();
      if (Utils.isFunction(result.implanted)) {
        result.implanted();
      }
    } catch (error) {
      console.error(error);
    }
    return result;
  }

  implant() {
    // this.query
    const isScoped = this.style.getAttribute('scoped') || (this.style.getAttribute('scoped') === '');
    const parent = this.selector.parentElement;
    // template
    this.template.setAttribute('query', this.query);
    parent.replaceChild(this.template, this.selector);
    if (this.style) {
      if (isScoped) {
        // scoped style
        parent.insertBefore(this.style, parent.childNodes[0]);
      } else {
        // global style
        this.style.setAttribute('component', this.query);
        const existStyle = document.querySelector(`style[component=${this.query}]`);
        if (existStyle) {
          document.querySelector('head').replaceChild(this.style, existStyle);
        } else {
          document.querySelector('head').appendChild(this.style);
        }
      }
    }
    return this;
  }

  static pjaxFormatHtml(url) {
    // pjaxhtml {template, style} templatehtml<body> style<style>
    const promise = pjax.get(url).then((result) => {
      const html = document.createElement('html');
      html.innerHTML = result;
      const styles = html.querySelectorAll('style');
      const body = html.querySelector('body');
      if (styles.length > 1) { throw new TypeError('<style>'); }
      if (body.childElementCount !== 1) { throw new TypeError('<body>'); }
      const template = body.firstElementChild;
      const style = styles[0];
      return { template, style };
    });
    return promise;
  }

  static of(param) {
    return new Component(param);
  }

  static pjaxCreate(param) {
    // param.urlhtml pjaxhtmlComponent
    if (!Utils.isString(param.url)) { throw new TypeError('param.urlhtml'); }
    const promise = Component.pjaxFormatHtml(param.url).then((format) => {
      const parameter = Object.assign(param, format);
      const cpt = Component.of(parameter);
      return cpt;
    });
    return promise;
  }
}


export default Component;
