
const indexedDB = window.indexedDB ||
  window.mozIndexedDB ||
  window.webkitIndexedDB ||
  window.msIndexedDB;
const IDBTransaction = window.IDBTransaction ||
  window.webkitIDBTransaction ||
  window.msIDBTransaction;
const IDBKeyRange = window.IDBKeyRange ||
  window.webkitIDBKeyRange ||
  window.msIDBKeyRange;

class Store {
  constructor(storeName) {
    if (!indexedDB || !IDBTransaction || !IDBKeyRange) throw new Error('indexedDB');
    this.name = this.name || storeName;
    this.databaseName = 'myEncapsulation';
    return this;
  }

  setDB(database) {
    if (!database || !(database instanceof IDBDatabase)) throw new TypeError('');
    this.database = database;
    this.version = this.database.version;
    this.database.onclose = () => {
      this.database.isOpen = false;
    };
    return database;
  }

  closeDB() {
    return this.database && (this.database instanceof IDBDatabase) && this.database.close();
  }

  openDB(version) {
    // openDB()
    // openDB(version) version int openDB(99)
    //  promise  resolve(this.database) reject(error);
    if (version) {
      if (!Number.isSafeInteger(version)) throw new TypeError('');
      if (version < this.version) throw new TypeError('');
    }
    const openVersion = (version && Number.isSafeInteger(version) && (version > this.version))
      ? version : this.version;
    this.closeDB();
    const promise = new Promise((resolve, reject) => {
      // const request = indexedDB.open(this.databaseName, openVersion);
      const request = (version && Number.isSafeInteger(version) && (version > this.version))
        ? indexedDB.open(this.databaseName, openVersion)
        : indexedDB.open(this.databaseName);
      request.onsuccess = () => {
        this.setDB(request.result);
        this.database.isOpen = true;
        resolve(this.database);
      };
      request.onerror = (event) => { reject(event.target.error); };
      request.onupgradeneeded = (event) => {
        this.setDB(event.target.result);
        const tx = event.target.transaction;
        if (!tx.objectStoreNames.contains(this.name)) {
          this.database.createObjectStore(this.name, { keyPath: 'id', autoIncrement: true });
          tx.objectStore(this.name).createIndex('id', 'id', { unique: true });
        }
      };
    });
    return promise;
  }

  ready() {
    return this.openDB().then(database => this.setDB(database));
  }

  isReady() {
    return this.database && (this.database instanceof IDBDatabase) && this.database.isOpen;
  }

  addColumn(column, option) {
    // addColumn(columnName) columnName,String,addColumn('age')
    // addColumn(columnName, option) option,addColumn('name', { unique: true })
    // promise resolve(database) databasedatabase
    if (!column || (typeof column !== 'string')) throw new TypeError('');
    if (option && (typeof option !== 'object')) throw new TypeError('');
    const columnOption = {};
    columnOption.unique = option && option.unique;
    const openVersion = this.version + 1;
    this.closeDB();
    const promise = new Promise((resolve, reject) => {
      const request = indexedDB.open(this.databaseName, openVersion);
      request.onsuccess = () => {
        this.setDB(request.result);
        this.database.isOpen = true;
        resolve(this.database);
      };
      request.onerror = (event) => { reject(event.target.error); };
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        const tx = event.target.transaction;
        this.database = db;
        this.version = this.database.version;
        const store = (tx && tx.objectStoreNames.contains(this.name))
          ? tx.objectStore(this.name)
          : db.createObjectStore(this.name, { keyPath: 'id', autoIncrement: true });
        if (!store.indexNames.contains('id')) {
          store.createIndex('id', 'id', { unique: true });
        }
        if (!store.indexNames.contains(column)) {
          store.createIndex(column, column, { unique: columnOption.unique });
        }
      };
    });
    return promise;
  }

  deleteColumn(column) {
    // deleteColumn(column) columnName,String,deleteColumn('userName')
    // promise resolve(database) databasedatabase
    if (!column || (typeof column !== 'string')) throw new TypeError('');
    this.closeDB();
    const openVersion = this.version + 1;
    const promise = new Promise((resolve, reject) => {
      const request = indexedDB.open(this.databaseName, openVersion);
      request.onsuccess = () => {
        this.setDB(request.result);
        this.database.isOpen = true;
        resolve(this.database);
      };
      request.onerror = (event) => {
        this.database = request.result;
        this.version = request.result.version;
        this.database.close();
        reject(event.target.error);
      };
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        const tx = event.target.transaction;
        this.database = db;
        this.version = this.database.version;
        const store = (tx && tx.objectStoreNames.contains(this.name))
          ? tx.objectStore(this.name)
          : db.createObjectStore(this.name, { keyPath: 'id', autoIncrement: true });
        if (!store.indexNames.contains('id')) {
          store.createIndex('id', 'id', { unique: true });
        }

        if (store.indexNames.contains(column)) {
          store.deleteIndex(column);
        }
      };
    });
    return promise;
  }

  addColumns(columns) {
    // addColumns(['userName'])
    // addColumns([{ index: 'userAge', unique: false }])
    // addColumns(['userName', { index: 'userAge', unique: false }])
    // promise resolve(database) databasedatabase
    if (!columns || !(columns instanceof Array)) throw new TypeError('');
    const newColumns = [];
    columns.forEach((column) => {
      const columnType = typeof column;
      if (columnType === 'string') {
        newColumns.push({ index: column, unique: false });
      } else if (columnType === 'object') {
        newColumns.push({ index: column.index, unique: column.unique });
      } else {
        throw new TypeError('');
      }
    });
    this.closeDB();
    const openVersion = this.version + 1;
    const promise = new Promise((resolve, reject) => {
      const request = indexedDB.open(this.databaseName, openVersion);
      request.onsuccess = () => {
        this.setDB(request.result);
        this.database.isOpen = true;
        resolve(this.database);
      };
      request.onerror = (event) => {
        this.database = request.result;
        this.version = request.result.version;
        this.database.close();
        reject(event.target.error);
      };
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        const tx = event.target.transaction;
        this.database = db;
        this.version = this.database.version;
        const store = (tx && tx.objectStoreNames.contains(this.name))
          ? tx.objectStore(this.name)
          : db.createObjectStore(this.name, { keyPath: 'id', autoIncrement: true });
        if (!store.indexNames.contains('id')) {
          store.createIndex('id', 'id', { unique: true });
        }
        newColumns.forEach((column) => {
          if (!store.indexNames.contains(column.index)) {
            store.createIndex(column.index, column.index, { unique: column.unique });
          }
        });
        this.database = db;
      };
    });
    return promise;
  }

  deleteColumns(columns) {
    // demo.deleteColumns(['userName', 'userAge'])
    // promise resolve(database) databasedatabase
    if (!columns || !(columns instanceof Array)) throw new TypeError('');
    columns.forEach((column) => {
      if (!column || (typeof column !== 'string')) throw new TypeError('');
    });
    this.closeDB();
    const openVersion = this.version + 1;
    const promise = new Promise((resolve, reject) => {
      const request = indexedDB.open(this.databaseName, openVersion);
      request.onsuccess = () => {
        this.setDB(request.result);
        this.database.isOpen = true;
        resolve(this.database);
      };
      request.onerror = (event) => {
        this.database = request.result;
        this.version = request.result.version;
        this.database.close();
        reject(event.target.error);
      };
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        const tx = event.target.transaction;
        this.database = db;
        this.version = this.database.version;
        const store = (tx && tx.objectStoreNames.contains(this.name))
          ? tx.objectStore(this.name)
          : db.createObjectStore(this.name, { keyPath: 'id', autoIncrement: true });
        if (!store.indexNames.contains('id')) {
          store.createIndex('id', 'id', { unique: true });
        }
        columns.forEach((column) => {
          if (store.indexNames.contains(column)) {
            store.deleteIndex(column);
          }
        });
      };
    });
    return promise;
  }

  deleteItem(id) {
    // deleteItem(id) idid Integer,deleteItem(1)
    // promise resolve(undefined)
    if (!Number.isSafeInteger(id)) throw new TypeError('id');
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const promise = new Promise((resolve, reject) => {
      const request = store.delete(id);
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    return promise;
  }

  deleteItems(ids) {
    // deleteItems(ids) idsid Array,deleteItems([1,2,3])
    // promise resolve(undefined)
    if (!(ids instanceof Array) || (ids.length === 0)) throw new TypeError('id');
    ids.forEach((id) => {
      if (!Number.isSafeInteger(id)) throw new TypeError('id');
    });
    let promise = this.ready();
    ids.forEach((id) => {
      promise = promise.then(() => this.deleteItem(id));
    });
    return promise;
  }

  delete(args) {
    // delete(id) idid Integer,delete(1)
    // delete(ids) idsid Array,delete([1,2,3])
    // promise resolve(undefined)
    const isInt = Number.isSafeInteger(args);
    const isArray = args instanceof Array;
    if (!isInt && !isArray) throw new TypeError('idid');
    let promise;
    if (isInt) {
      promise = this.deleteItem(args);
    } else if (isArray) {
      promise = this.deleteItems(args);
    }
    return promise;
  }

  setItem(item) {
    // setItem(item) item, Object, setItem({ id: 3, name: 'xiaoming', age: 10 })
    // iditem.id(Integer),item.idid
    // promise
    // resolve() item.id
    if (!item || (typeof item !== 'object')) throw new TypeError('');
    if ((item.id !== undefined) && !Number.isSafeInteger(item.id)) throw new TypeError('id');
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const promise = new Promise((resolve, reject) => {
      const request = store.put(item);
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    return promise;
  }

  setItems(items) {
    // setItems(items) itemsArray,[{id:1, age:12}, {name: 'x', age: 12}]
    // promise
    // resolve() item.id
    if (!items || !(items instanceof Array)) throw new TypeError('');
    items.forEach((item) => {
      if (!item || (typeof item !== 'object')) throw new TypeError('');
    });
    if (!this.isReady()) throw new Error('');
    let promise = this.ready();
    items.forEach((item) => {
      promise = promise.then(() => this.setItem(item));
    });
    return promise;
  }

  set(args) {
    // set(item) setItem(item)
    // set(items) setItems(items)
    const isArray = args instanceof Array;
    const isObj = (typeof args === 'object');
    if (!isObj && !isArray) throw new TypeError('idid');
    let result;
    if (isArray) {
      result = this.setItems(args);
    } else if (isObj) {
      result = this.setItem(args);
    }
    return result;
  }

  getItem(id) {
    // getItem(id) idid Integer,getItem(1) id1
    // promise resolve(item) itemundefiend
    if (!Number.isSafeInteger(id)) throw new TypeError('id');
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const promise = new Promise((resolve, reject) => {
      const request = store.get(id);
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    return promise;
  }

  getItems(ids) {
    // getItems(ids) idsid Array,getItems([1,2,3])
    // promise resolve(items) items
    if (!(ids instanceof Array) || (ids.length === 0)) throw new TypeError('id');
    ids.forEach((id) => {
      if (!Number.isSafeInteger(id)) throw new TypeError('id');
    });
    const sortIds = ids.sort();
    const range = IDBKeyRange.bound(sortIds[0], sortIds[sortIds.length - 1]);
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const promise = new Promise((resolve, reject) => {
      const request = store.getAll(range);
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    const resPromise = promise.then((items) => {
      const result = [];
      items.forEach((item) => {
        if (sortIds.includes(item.id)) {
          result.push(item);
        }
      });
      return result;
    });
    return resPromise;
  }

  get(idQuery) {
    // get(id) idid Integer,get(1) getItem(id)
    // promise resolve(item) itemundefiend
    // get(ids) idsid Array,get([1,2,3]) getItems(ids)
    // promise resolve(items) items
    const isInt = Number.isSafeInteger(idQuery);
    const isArray = idQuery instanceof Array;
    if (!isInt && !isArray) throw new TypeError('idid');
    let promise;
    if (isInt) {
      promise = this.getItem(idQuery);
    } else if (isArray) {
      promise = this.getItems(idQuery);
    }
    return promise;
  }

  getAll(indexName) {
    // getAll(indexName) indexName,getAll('id')
    // promise resolve(items) itemsindexName
    const openIndexName = indexName || 'id';
    if (typeof openIndexName !== 'string') throw new TypeError('');
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const index = store.index(openIndexName);
    const promise = new Promise((resolve, reject) => {
      const request = index.getAll();
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    return promise;
  }

  findItems(attrQuery) {
    // findItem(attrQuery) attrQuery,Object,findItem({id: 1, name: 'joy'})
    // promise resolve(items) items
    if (!attrQuery || (typeof attrQuery !== 'object')) throw new TypeError('');
    if (!this.isReady()) throw new Error('');
    const store = this.database.transaction(this.name, 'readwrite').objectStore(this.name);
    const keys = Object.keys(attrQuery);
    const firstIndex = store.index(keys[0]);
    const range = IDBKeyRange.only(attrQuery[keys[0]]);
    const promise = new Promise((resolve, reject) => {
      const request = firstIndex.getAll(range);
      request.onsuccess = () => { resolve(request.result); };
      request.onerror = (event) => { reject(event.target.error); };
    });
    const resPromise = promise.then((items) => {
      const result = [];
      items.forEach((item) => {
        let isEqual = 1;
        keys.forEach((key) => {
          if (item[key] !== attrQuery[key]) isEqual = 0;
        });
        if (isEqual) {
          result.push(item);
        }
      });
      return result;
    });
    return resPromise;
  }

  static openDB(databaseName) {
    // openDB(databaseName) databaseName string
    // promise resolve(IDBDatabase) reject(Error)
    if (!databaseName || (typeof databaseName !== 'string')) throw new TypeError(`${databaseName}`);
    const promise = new Promise((resolve, reject) => {
      const request = indexedDB.open(databaseName);
      request.onsuccess = () => {
        const database = request.result;
        database.isOpen = true;
        database.onclose = () => { database.isOpen = false; };
        resolve(database);
      };
      request.onerror = event => reject(event.target.error);
    });
    return promise;
  }
}

export default Store;
